import { unref as _unref, renderList as _renderList, Fragment as _Fragment, openBlock as _openBlock, createElementBlock as _createElementBlock, createBlock as _createBlock, withCtx as _withCtx, createVNode as _createVNode, withScopeId as _withScopeId } from "vue"

import { ref, computed } from 'vue'
import { searchList as useSearchList, getSong, getImg } from '@/apis/qq'
import { singerItem, songItemInter, songListDetailMap } from '@/detail/qq'
import { List, Toast } from 'vant'
import Song from '@/components/song/index.vue'

export interface qqRefInterface {
    search:(text: string)=>void
}

export default _defineComponent({
  setup(__props, { expose }) {

const singerName = (singer?: singerItem[]):string => {
  const list = singer || []
  const singerNameList = list.map(e => {
    return e.name
  })
  return singerNameList.join(' / ')
}
let eeui:any = null
if (window.requireModuleJs) {
  eeui = window.requireModuleJs('eeui')
}
