const pageSize = 20
const loadError = ref<boolean>(false)
const { request: getSearchList, loading } = useSearchList()

const finished = computed(() => {
  if (loadError.value === true) {
    return true
  }
  return (page.value * pageSize) > total.value
})
const getList = () => {
  const params = {
    w: query.value,
    n: pageSize,
    p: page.value + 1
  }
  getSearchList({ params }).then(({ data }) => {
    const song = data?.data.song || {}
    const list = song?.list || []
    const totalCount = song?.totalnum || 0
    total.value = totalCount
    page.value = page.value + 1
