  getSong(song).then((res) => {
    const data = res?.data?.req_0?.data || {}
    const source = data?.midurlinfo?.[0]?.purl
    const songUrl = source && `${data?.sip?.[0] || ''}${data?.midurlinfo?.[0]?.purl || ''}`
    if (!songUrl) {
      Toast.fail('获取资源失败')
    }
    const params = {
      pageName: 'index',
      module: 'playSong',
      songUrl,
      songImage: imageUrl,
      singer: singerName(song.singer),
      songname: song.songname
    }
    eeui !== null && eeui.postMessage(params)
  })
